:root {
  --green-color: rgb(6, 214, 6);
}

body {
  margin: 0;
}

.Report-List {
  display: flex;
  place-content: center;
}

.container {
  margin-top: 1.5rem;
  margin-bottom: 5rem;
  border: 1px solid black;
  padding: 1rem;
}

.heading {
  text-align: center;
  margin-bottom: 1.5rem;
}

.heading h2 {
  margin-bottom: 0.5rem;
}

.row {
  display: grid;
  gap: 1rem;
  width: 25rem;
}

.input-group {
  display: flex;
}

.input-group h3 {
  margin: 0.6rem 0;
}

.input-group h3:nth-child(2) {
  margin-left: auto;
  margin-right: 3rem;
}

.input-group input {
  padding: 3px 10px;
  border: 1px solid rgb(42, 42, 42);
}

.conform {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
}

.conform button {
  width: 100%;
  cursor: pointer;
  padding: 0.6rem 2rem;
  background: var(--green-color);
  color: white;
  border: 0;
  font-size: 15px;
  font-weight: 700;
}

.conform button:hover {
  background: rgb(6, 152, 6);
  border: 1px solid black;
}

/* ====== List ======= */
.list {
  box-sizing: border-box;
  position: relative;
}

.list h3 {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}

.card {
  padding: 1rem;
}

.card textarea {
  padding: 1rem;
  width: -webkit-fill-available;
  border: 1px solid rgb(42, 42, 42);
}

.Offer {
  background: rgb(244, 1, 1);
  color: rgb(255, 255, 255);
  padding: 0.4rem 1rem;
  font-weight: 700;
  cursor: pointer;
  border: 0;
}

.Offer:hover {
  background: var(--green-color);
}

.Copy-btn {
  display: flex;
  place-content: center;
}

.Copy-btn button {
  width: 40%;
  padding: 3px 5px;
}

.Copy-icon {
  padding: 10px;
  display: table-caption;
  position: absolute;
  top: 2.5rem;
  right: 0.6rem;
  cursor: pointer;
}

.Copy-icon:hover {
  color: var(--green-color);
}

@media (max-width:434px) {
  .container {
    padding: 0.4rem;
  }

  .heading h2 {
    font-size: 1.15rem;
  }

  .row {
    width: 21rem;
  }

  .input-group h3 {
    font-size: 0.9rem;
  }
}

@media (max-width:355px) {
  .container {
    padding-bottom: 1rem;
  }

  .heading h2 {
    font-size: 1rem;
  }

  .row {
    width: fit-content;
  }

  .input-group h3:nth-child(2) {
    margin-right: 0.5rem;
  }

  .input-group input {
    width: 8rem;
  }

  .card {
    padding: 0;
    margin-bottom: 0.5rem;
  }

  .card textarea {
    padding: 0.5rem;
  }

  .Copy-icon {
    top: 1.8rem;
    right: 0.1rem;
  }
}


/* Footer  */
footer {
  text-align: center;
  font-size: 12px;
  padding: 1rem;
  background-color: rgb(51, 50, 50);
  color: white;
}

footer span {
  cursor: pointer;
}